@import url(https://fonts.googleapis.com/css?family=Gothic+A1:400,700,900|Open+Sans&display=swap);
:root {
  --black: #0D0D0D;
  --lightest-grey: #E6E6E6;
  --light-grey: #D5D5D5;
  --grey: #939393;
  --dark-grey: rgb(121, 121, 121);
  --synth1Color: #26C6C6;
  --bassColor: #FF6AEE;
  --lennyColor: yellow;
}

.piano-roll {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  width: 75%;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.piano-roll-editor {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
}

.step-row {
  height: 26px;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.step-row.black {
  background-color: #939393;
  background-color: var(--grey);
}

.step-row.white {
  background-color: #E6E6E6;
  background-color: var(--lightest-grey);
}
.step-row.white:nth-of-type(odd) {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
}

.piano-cell {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-right: 1px solid rgb(121, 121, 121);
  border-right: 1px solid var(--dark-grey);
  font-family: 'Roboto';
  font-size: 10px;
}

.cell-trigger-synth1 {
  background-color: #26C6C6;
  background-color: var(--synth1Color);
  border-radius: 5px;
  border-color: #26C6C6;
  border-color: var(--synth1Color);
}

.cell-trigger-bass {
  background-color: #FF6AEE;
  background-color: var(--bassColor);
  border-radius: 5px;
  border-color: #FF6AEE;
  border-color: var(--bassColor);
}

.cell-trigger-lenny {
  background-color: yellow;
  background-color: var(--lennyColor);
  border-radius: 5px;
  border-color: yellow;
  border-color: var(--lennyColor);
}

.cell-trigger {
  width: 100%;
  height: 100%;
}

.piano-cell p::after {
  content: '%';
}

/* KEYS */

.piano-roll-keys {
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
}

.piano-key {
  height: 26px;
  width: 40px;
  padding: 0px;;
  margin: 0px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.piano-key.black {
  background-color: #0D0D0D;
  background-color: var(--black);
  color: #D5D5D5;
  color: var(--light-grey);
}

.piano-key.white {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
  color: #0D0D0D;
  color: var(--black);
}
.piano-key p {
  font-family: 'Roboto';
  font-size: 10px;
  padding: 0;
  margin: 0;
}
html {
  font-family: 'Open Sans', sans-serif;
}

h1 {
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-weight: 900;
}

h2 {
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-size: 24px;
  font-weight: 900;
}



#sequencer {
  width: 600px;
  height: 420px;
  margin: 0 auto 80px auto;
}

#amp-env {
  text-align: center;
}

#filter-env {
  text-align: center;
}

#play-button {
  width: 60px;
  margin: auto;
  display: block;
}

#synth-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.synth {
  border: 1px solid lightgray;
  border-radius: 8px;
  width: 40%;
  display: inline-block;
  margin: 16px;
}

.knob-row {
  margin: auto;
  display: flex;
  justify-content: center;
}

.knob {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
  padding: 8px;
  line-height: 2;
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-weight: 500;
  font-size: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
